e Daten werden gesperrt und nicht für andere Zwecke verarbeitet. Das gilt z.B. für Daten, die aus handels- oder steuerrechtlichen Gründen aufbewahrt werden müssen. ")]),_c('h3',[_vm._v("Änderungen und Aktualisierungen der Datenschutzerklärung")]),_c('p',[_vm._v(" Wir bitten Sie sich regelmäßig über den Inhalt unserer Datenschutzerklärung zu informieren. Wir passen die Datenschutzerklärung an, sobald die Änderungen der von uns durchgeführten Datenverarbeitungen dies erforderlich machen. Wir informieren Sie, sobald durch die Änderungen eine Mitwirkungshandlung Ihrerseits (z.B. Einwilligung) oder eine sonstige individuelle Benachrichtigung erforderlich wird. ")]),_c('h3',[_vm._v(" Administration, Finanzbuchhaltung, Büroorganisation, Kontaktverwaltung ")]),_c('p',[_vm._v(" Wir verarbeiten Daten im Rahmen von Verwaltungsaufgaben sowie Organisation unseres Betriebs, Finanzbuchhaltung und Befolgung der gesetzlichen Pflichten, wie z.B. der Archivierung. Hierbei verarbeiten wir dieselben Daten, die wir im Rahmen der Erbringung unserer vertraglichen Leistungen verarbeiten. Die Verarbeitungsgrundlagen sind Art. 6 Abs. 1 lit. c. DSGVO, Art. 6 Abs. 1 lit. f. DSGVO. Von der Verarbeitung sind Kunden, Interessenten, Geschäftspartner und Websitebesucher betroffen. Der Zweck und unser Interesse an der Verarbeitung liegt in der Administration, Finanzbuchhaltung, Büroorganisation, Archivierung von Daten, also Aufgaben die der Aufrechterhaltung unserer Geschäftstätigkeiten, Wahrnehmung unserer Aufgaben und Erbringung unserer Leistungen dienen. Die Löschung der Daten im Hinblick auf vertragliche Leistungen und die vertragliche Kommunikation entspricht den, bei diesen Verarbeitungstätigkeiten genannten Angaben. ")]),_c('p',[_vm._v(" Wir offenbaren oder übermitteln hierbei Daten an die Finanzverwaltung, Berater, wie z.B., Steuerberater oder Wirtschaftsprüfer sowie weitere Gebührenstellen und Zahlungsdienstleister. ")]),_c('p',[_vm._v(" Ferner speichern wir auf Grundlage unserer betriebswirtschaftlichen Interessen Angaben zu Lieferanten, Veranstaltern und sonstigen Geschäftspartnern, z.B. zwecks späterer Kontaktaufnahme. Diese mehrheitlich unternehmensbezogenen Daten, speichern wir grundsätzlich dauerhaft. ")]),_c('h3',[_vm._v("Verarbeitung personenbezogener Daten für die Zahlungsabwicklung")]),_c('p',[_vm._v(" Für die Bearbeitung Ihres Auftrages arbeiten wir mit den folgenden Dienstleistern zusammen, die uns ganz oder teilweise bei der Abwicklung der abgeschlossenen Verträge unterstützen. Bestimmte personenbezogene Daten werden an diese Dienstleister gemäß den folgenden Informationen übermittelt. ")]),_c('p',[_vm._v(" Soweit Zahlungsdienstleister in Anspruch genommen werden, weisen wir Sie im Folgenden ausdrücklich darauf hin. Rechtsgrundlage für die Übermittlung der Daten ist Art. 6 Abs. 1 Buchstabe b D. Eine Weitergabe Ihrer Zahlungsdaten an das mit der Lieferung beauftragte Kreditinstitut erfolgt im Rahmen der Zahlungsabwicklung, soweit dies zur Zahlungsabwicklung erforderlich ist.Sofern Zahlungsdienstleister in Anspruch genommen werden, verpflichten wir uns ausdrücklich teilen Ihnen dies unten mit. Rechtsgrundlage für die Übermittlung der Daten ist Art. 6 Abs. 1 Buchstabe b GDPR. ")]),_c('h4',[_vm._v("Stripe")]),_c('p',[_vm._v(" Wenn Sie eine Stripe-Zahlungsmethode wählen, wird die Zahlung durch den Zahlungsdienstleister Stripe Payments Europe Ltd, Block 4, Harcourt Centre, Harcourt Road, Dublin 2, Irland, abgewickelt, an den wir Ihre während des Bestellvorgangs gemachten Angaben zusammen mit den Informationen zu Ihrer Bestellung (Name, Adresse, Kontonummer, Bankleitzahl, ggf. Bankleitzahl, Kreditkartennummer, Rechnungsbetrag, Währung und Transaktionsnummer) gemäß Art. 6 Absatz 1 Buchstabe b GDPR. Eine Weitergabe Ihrer Daten erfolgt nur zum Zwecke der Zahlungsabwicklung mit dem Zahlungsdienstleister Stripe Payments Europe Ltd. und nur im notwendigen Umfang. Für weitere Informationen über die Datenschutzpolitik von Stripe besuchen Sie bitte: "),_c('a',{attrs:{"href":"https://stripe.com/de/privacy","rel":"noreferrer noopener","target":"_blank"}},[_vm._v("https://stripe.com/de/privacy")])]),_c('h3',[_vm._v(" Verarbeitung personenbezogener Daten für Bewerberinnen und Bewerber ")]),_c('p',[_c('strong',[_vm._v("Welche Daten verarbeiten wir?")])]),_c('p',[_vm._v(" Wenn Sie sich bei MFA mal anders bewerben, werden grundsätzlich nur solche Daten verarbeitet, die Sie uns im Rahmen der Bewerbung zur Verfügung gestellt haben. In der Regel verarbeiten wir so Ihre Kontaktdaten (Name, Vorname, E-Mail-Adresse) und persönliche Informationen, die sich aus dem Anschreiben und dem Lebenslauf (Bewerbungsdaten) ergeben. ")]),_c('p',[_c('strong',[_vm._v("Besteht für mich eine Pflicht zur Bereitstellung von Daten")])]),_c('p',[_vm._v(" Es besteht keine gesetzliche oder vertragliche Pflicht zur Bereitstellung von Daten. Im Rahmen Ihrer Bewerbung sollen Sie nur diejenigen personenbezogenen Daten bereitstellen, die für die Aufnahme und Durchführung der Bewerbung erforderlich sind. Ohne diese Daten werden wir jedoch Ihre Aufnahme in den Bewerbungsprozess ablehnen müssen. ")]),_c('p',[_c('strong',[_vm._v("Für welchen Zweck verarbeiten wir Ihre personenbezogenen Daten?")])]),_c('p',[_vm._v(" Wir verarbeiten Ihre Daten, um Ihre Eignung für die ausgeschriebene Stelle oder ggf. eine andere offene Position in unserem Unternehmen zu prüfen und um das Bewerbungsverfahren durchzuführen. ")]),_c('p',[_c('strong',[_vm._v("Auf welcher Rechtsgrundlage stützen wir die Verarbeitung Ihrer personenbezogenen Daten?")])]),_c('p',[_vm._v(" Die Rechtsgrundlage für die Verarbeitung Ihrer personenbezogenen Daten in diesem Bewerbungsverfahren ist Art. 88 DSGVO i.V.m. § 26 BDSG. Danach ist die Verarbeitung der Daten zulässig, die im Zusammenhang mit der Entscheidung über die Begründung eines Beschäftigungsverhältnisses erforderlich sind. ")]),_c('p',[_vm._v(" Insbesondere Daten, die wir über das Bewerbungsverfahren hinausgehend verarbeiten, werden i.d.R. aufgrund des Art. 6 Abs. 1 S. 1 lit. c) DSGVO verarbeitet. Dies ist der Fall, wenn wir gesetzlich verpflichtet sind, Ihre Daten z.B. aus steuerrechtlichen Gründen weiter zu verarbeiten (z.B. Erstattung der Anreisekosten). ")]),_c('p',[_c('strong',[_vm._v("An welche Empfänger geben wir die Daten weiter?")])]),_c('p',[_vm._v(" Ihre Bewerberdaten werden nach Eingang Ihrer Bewerbung von der Personalabteilung und den entsprechenden Führungskräften gesichtet. Geeignete Bewerbungen werden dann intern an die Abteilungsverantwortlichen für die jeweils offene Position weitergeleitet. Im Unternehmen haben grundsätzlich nur die Personen Zugriff auf Ihre Daten, die dies für den ordnungsgemäßen Ablauf unseres Bewerbungsverfahrens benötigen. ")]),_c('p',[_c('strong',[_vm._v("Wie lange werden die Daten gespeichert?")])]),_c('p',[_vm._v(" Daten aus Ihrer Bewerbung werden im Falle einer Absage spätestens nach einer Frist von 6 Monaten gelöscht. Selbstverständlich können Sie Ihre Bewerbung auch zurückziehen und wir löschen Ihre personenbezogenen Daten unverzüglich, sofern der Löschung keine gesetzlichen Aufbewahrungsfristen entgegenstehen. ")]),_c('p',[_vm._v(" Bei einer Einwilligung zu längerfristiger Speicherung in unserem Bewerberpool gilt die mit Ihnen vereinbarte Speicherdauer. Diese beträgt in der Regel 2 Jahre. ")]),_c('p',[_vm._v(" Bei einer Einstellung speichern wir Ihre Bewerbungsunterlagen und führen diese in die Personalakte über. ")]),_c('p',[_c('strong',[_vm._v("Werden meine Daten an ein Drittland übermittelt?")])]),_c('p',[_vm._v(" Eine Übermittlung Ihrer Bewerberdaten in ein Drittland findet grundsätzlich nicht statt. Ausnahmsweise kann dies bei bestimmten Programmen der Fall sein, wenn wir diese zur Bewerberverwaltung benutzen. ")]),_c('p',[_c('strong',[_vm._v("Welche Datenschutzrechte habe ich?")])]),_c('p',[_vm._v(" Ihre Rechte ergeben sich aus den Art. 15 ff. DSGVO. Unter anderem haben Sie: ")]),_c('ul',[_c('li',[_vm._v("ein Auskunftsrecht nach Art. 15 DSGVO")]),_c('li',[_vm._v(" ein Recht auf Berichtigung Ihrer personenbezogenen Daten nach Art. 16 DSGVO ")]),_c('li',[_vm._v("ein Recht auf Löschung Art. 17 DSGVO")]),_c('li',[_vm._v(" ein Recht auf Einschränkung der Verarbeitung nach Art. 18 DSGVO ")]),_c('li',[_vm._v("sowie ein Recht auf Datenübertragbarkeit nach Art. 20 DSGVO")])]),_c('p',[_vm._v(" Zusätzlich können Sie in dem Fall, dass die Verarbeitung einem berechtigtem Interesse nach Art. 6 Abs. 1 S. 1 lit. f) der Verarbeitung nach Art. 21 DSGVO widersprechen. Jedoch werden die personenbezogenen Daten nur dann nicht mehr verarbeitet, wenn wir keine schutzwürdigen Gründe für die Verarbeitung nachweisen können, die Ihre Rechts- und Freiheitsinteressen überwiegen. Unbeschadet dessen bleibt Ihnen stets die Möglichkeit sich bei der zuständigen Datenschutzaufsichtsbehörde zu beschweren (Art. 77 DSGVO i.V.m. § 19 BDSG) oder einen gerichtlichen Rechtsbehelf einzulegen (Art. 78 DSGVO). ")]),_c('h3',[_vm._v("Registrierfunktion")]),_c('p',[_vm._v(" Nutzer können ein Nutzerkonto anlegen. Im Rahmen der Registrierung werden die erforderlichen Pflichtangaben den Nutzern mitgeteilt und auf Grundlage des Art. 6 Abs. 1 lit. b DSGVO zu Zwecken der Bereitstellung des Nutzerkontos verarbeitet. Zu den verarbeiteten Daten gehören insbesondere die Login-Informationen (Name, Passwort sowie eine E-Mailadresse). Die im Rahmen der Registrierung eingegebenen Daten werden für die Zwecke der Nutzung des Nutzerkontos und dessen Zwecks verwendet. ")]),_c('p',[_vm._v(" Die Nutzer können über Informationen, die für deren Nutzerkonto relevant sind, wie z.B. technische Änderungen, per E-Mail informiert werden. Wenn Nutzer ihr Nutzerkonto gekündigt haben, werden deren Daten im Hinblick auf das Nutzerkonto, vorbehaltlich einer gesetzlichen Aufbewahrungspflicht, gelöscht. Es obliegt den Nutzern, ihre Daten bei erfolgter Kündigung vor dem Vertragsende zu sichern. Wir sind berechtigt, sämtliche während der Vertragsdauer gespeicherten Daten des Nutzers unwiederbringlich zu löschen. ")]),_c('p',[_vm._v(" Im Rahmen der Inanspruchnahme unserer Registrierungs- und Anmeldefunktionen sowie der Nutzung des Nutzerkontos, speichern wir die IP-Adresse und den Zeitpunkt der jeweiligen Nutzerhandlung. Die Speicherung erfolgt auf Grundlage unserer berechtigten Interessen, als auch der Nutzer an Schutz vor Missbrauch und sonstiger unbefugter Nutzung. Eine Weitergabe dieser Daten an Dritte erfolgt grundsätzlich nicht, außer sie ist zur Verfolgung unserer Ansprüche erforderlich oder es besteht hierzu besteht eine gesetzliche Verpflichtung gem. Art. 6 Abs. 1 lit. c. DSGVO. Die IP-Adressen werden spätestens nach 7 Tagen anonymisiert oder gelöscht. ")]),_c('h3',[_vm._v("Kommentare und Beiträge")]),_c('p',[_vm._v(" Wenn Nutzer Kommentare oder sonstige Beiträge hinterlassen, können ihre IP-Adressen auf Grundlage unserer berechtigten Interessen im Sinne des Art. 6 Abs. 1 lit. f. DSGVO für 7 Tage gespeichert werden. Das erfolgt zu unserer Sicherheit, falls jemand in Kommentaren und Beiträgen widerrechtliche Inhalte hinterlässt (Beleidigungen, verbotene politische Propaganda, etc.). In diesem Fall können wir selbst für den Kommentar oder Beitrag belangt werden und sind daher an der Identität des Verfassers interessiert. ")]),_c('p',[_vm._v(" Des Weiteren behalten wir uns vor, auf Grundlage unserer berechtigten Interessen gem. Art. 6 Abs. 1 lit. f. DSGVO, die Angaben der Nutzer zwecks Spamerkennung zu verarbeiten. ")]),_c('p',[_vm._v(" Auf derselben Rechtsgrundlage behalten wir uns vor, im Fall von Umfragen die IP-Adressen der Nutzer für deren Dauer zu speichern und Cookies zu verwenden, um Mehrfachabstimmungen zu vermeiden. ")]),_c('p',[_vm._v(" Die im Rahmen der Kommentare und Beiträge mitgeteilte Informationen zur Person, etwaige Kontakt- sowie Websiteinformationen als auch die inhaltlichen Angaben, werden von uns bis zum Widerspruch der Nutzer dauerhaft gespeichert. ")]),_c('h3',[_vm._v("Kommentarabonnements")]),_c('p',[_vm._v(" Die Nachfolgekommentare können durch Nutzer mit deren Einwilligung gem. Art. 6 Abs. 1 lit. a DSGVO abonniert werden. Die Nutzer erhalten eine Bestätigungsemail, um zu überprüfen, ob sie der Inhaber der eingegebenen Emailadresse sind. Nutzer können laufende Kommentarabonnements jederzeit abbestellen. Die Bestätigungsemail wird Hinweise zu den Widerrufsmöglichkeiten enthalten. Für die Zwecke des Nachweises der Einwilligung der Nutzer, speichern wir den Anmeldezeitpunkt nebst der IP-Adresse der Nutzer und löschen diese Informationen, wenn Nutzer sich von dem Abonnement abmelden. ")]),_c('p',[_vm._v(" Sie können den Empfang unseres Abonnements jederzeit kündigen, d.h. Ihre Einwilligungen widerrufen. Wir können die ausgetragenen E-Mailadressen bis zu drei Jahren auf Grundlage unserer berechtigten Interessen speichern bevor wir sie löschen, um eine ehemals gegebene Einwilligung nachweisen zu können. Die Verarbeitung dieser Daten wird auf den Zweck einer möglichen Abwehr von Ansprüchen beschränkt. Ein individueller Löschungsantrag ist jederzeit möglich, sofern zugleich das ehemalige Bestehen einer Einwilligung bestätigt wird. ")]),_c('h3',[_vm._v("Teilnahme an Affiliate-Partnerprogrammen")]),_c('p',[_vm._v(" Innerhalb unseres Onlineangebotes setzen wir auf Grundlage unserer berechtigten Interessen (d.h. Interesse an der Analyse, Optimierung und wirtschaftlichem Betrieb unseres Onlineangebotes) gem. Art. 6 Abs. 1 lit. f DSGVO branchenübliche Trackingmaßnahmen ein, soweit diese für den Betrieb des Affiliatesystems erforderlich sind. Nachfolgend klären wir die Nutzer über die technischen Hintergründe auf. ")]),_c('p',[_vm._v(" Die von unseren Vertragspartnern angebotene Leistungen können auch auf anderen Webseiten beworben und verlinkt werden (sog. Affiliate-Links oder After-Buy-Systeme, wenn z.B. Links oder Leistungen Dritter nach einem Vertragsschluss angeboten werden). Die Betreiber der jeweiligen Webseiten erhalten eine Provision, wenn Nutzer den Affiliate-Links folgen und anschließend die Angebote wahrnehmen. ")]),_c('p',[_vm._v(" Zusammenfassend, ist es für unser Onlineangebot erforderlich, dass wir nachverfolgen können, ob Nutzer, die sich für Affiliate-Links und/oder die bei uns verfügbaren Angebote interessieren, die Angebote anschließend auf die Veranlassung der Affiliate-Links oder unserer Onlineplattform, wahrnehmen. Hierzu werden die Affiliate-Links und unsere Angebote um bestimmte Werte ergänzt, die ein Bestandteil des Links oder anderweitig, z.B. in einem Cookie, gesetzt werden können. Zu den Werten gehören insbesondere die Ausgangswebseite (Referrer), Zeitpunkt, eine Online-Kennung der Betreiber der Webseite, auf der sich der Affiliate-Link befand, eine Online-Kennung des jeweiligen Angebotes, eine Online-Kennung des Nutzers, als auch Tracking-spezifische Werte wie z.B. Werbemittel-ID, Partner-ID und Kategorisierungen. ")]),_c('p',[_vm._v(" Bei der von uns verwendeten Online-Kennungen der Nutzer, handelt es sich um pseudonyme Werte. D.h. die Online-Kennungen enthalten selbst keine personenbezogenen Daten wie Namen oder E-Mailadressen. Sie helfen uns nur zu bestimmen ob derselbe Nutzer, der auf einen Affiliate-Link geklickt oder sich über unser Onlineangebot für ein Angebot interessiert hat, das Angebot wahrgenommen, d.h. z.B. einen Vertrag mit dem Anbieter abgeschlossen hat. Die Online-Kennung ist jedoch insoweit personenbezogen, als dem Partnerunternehmen und auch uns, die Online-Kennung zusammen mit anderen Nutzerdaten vorliegen. Nur so kann das Partnerunternehmen uns mitteilen, ob derjenige Nutzer das Angebot wahrgenommen hat und wir z.B. den Bonus auszahlen können. ")]),_c('h3',[_vm._v("Amazon-Partnerprogramm")]),_c('p',[_vm._v(" Wir sind auf Grundlage unserer berechtigten Interessen (d.h. Interesse am wirtschaftlichem Betrieb unseres Onlineangebotes im Sinne des Art. 6 Abs. 1 lit. f. DSGVO) Teilnehmer des Partnerprogramms von Amazon EU, das zur Bereitstellung eines Mediums für Websites konzipiert wurde, mittels dessen durch die Platzierung von Werbeanzeigen und Links zu Amazon.de Werbekostenerstattung verdient werden kann (sog. Affiliate-System). D.h. als Amazon-Partner verdienen wir an qualifizierten Käufen. ")]),_c('p',[_vm._v(" Amazon setzt Cookies ein, um die Herkunft der Bestellungen nachvollziehen zu können. Unter anderem kann Amazon erkennen, dass Sie den Partnerlink auf dieser Website geklickt und anschließend ein Produkt bei Amazon erworben haben. ")]),_c('p',[_vm._v(" Weitere Informationen zur Datennutzung durch Amazon und Widerspruchsmöglichkeiten erhalten Sie in der Datenschutzerklärung des Unternehmens: "),_c('a',{attrs:{"href":"https://www.amazon.de/gp/help/customer/display.html?nodeId=201909010","rel":"noreferrer noopener","target":"_blank"}},[_vm._v("https://www.amazon.de/gp/help/customer/display.html?nodeId=201909010")]),_vm._v(". ")]),_c('p',[_vm._v(" Hinweis: Amazon und das Amazon-Logo sind Warenzeichen von Amazon.com, Inc. oder eines seiner verbundenen Unternehmen. ")]),_c('h3',[_vm._v("Kontaktaufnahme")]),_c('p',[_vm._v(" Bei der Kontaktaufnahme mit uns (z.B. per Kontaktformular, E-Mail, Telefon oder via sozialer Medien) werden die Angaben des Nutzers zur Bearbeitung der Kontaktanfrage und deren Abwicklung gem. Art. 6 Abs. 1 lit. b. (im Rahmen vertraglicher-/vorvertraglicher Beziehungen), Art. 6 Abs. 1 lit. f. (andere Anfragen) DSGVO verarbeitet.. Die Angaben der Nutzer können in einem Customer-Relationship-Management System („CRM System“) oder vergleichbarer Anfragenorganisation gespeichert werden. ")]),_c('p',[_vm._v(" Wir löschen die Anfragen, sofern diese nicht mehr erforderlich sind. Wir überprüfen die Erforderlichkeit alle zwei Jahre; Ferner gelten die gesetzlichen Archivierungspflichten. ")]),_c('h3',[_vm._v("Newsletter")]),_c('p',[_vm._v(" Mit den nachfolgenden Hinweisen informieren wir Sie über die Inhalte unseres Newsletters sowie das Anmelde-, Versand- und das statistische Auswertungsverfahren sowie Ihre Widerspruchsrechte auf. Indem Sie unseren Newsletter abonnieren, erklären Sie sich mit dem Empfang und den beschriebenen Verfahren einverstanden. ")]),_c('p',[_vm._v(" Inhalt des Newsletters: Wir versenden Newsletter, E-Mails und weitere elektronische Benachrichtigungen mit werblichen Informationen (nachfolgend „Newsletter“) nur mit der Einwilligung der Empfänger oder einer gesetzlichen Erlaubnis. Sofern im Rahmen einer Anmeldung zum Newsletter dessen Inhalte konkret umschrieben werden, sind sie für die Einwilligung der Nutzer maßgeblich. Im Übrigen enthalten unsere Newsletter Informationen zu unseren Leistungen und uns. ")]),_c('p',[_vm._v(" Double-Opt-In und Protokollierung: Die Anmeldung zu unserem Newsletter erfolgt in einem sog. Double-Opt-In-Verfahren. D.h. Sie erhalten nach der Anmeldung eine E-Mail, in der Sie um die Bestätigung Ihrer Anmeldung gebeten werden. Diese Bestätigung ist notwendig, damit sich niemand mit fremden E-Mailadressen anmelden kann. Die Anmeldungen zum Newsletter werden protokolliert, um den Anmeldeprozess entsprechend den rechtlichen Anforderungen nachweisen zu können. Hierzu gehört die Speicherung des Anmelde- und des Bestätigungszeitpunkts, als auch der IP-Adresse. Ebenso werden die Änderungen Ihrer bei dem Versanddienstleister gespeicherten Daten protokolliert. ")]),_c('p',[_vm._v(" Anmeldedaten: Um sich für den Newsletter anzumelden, reicht es aus, wenn Sie Ihre E-Mailadresse angeben. Optional bitten wir Sie einen Namen, zwecks persönlicher Ansprache im Newsletters anzugeben. ")]),_c('p',[_vm._v(" Der Versand des Newsletters und die mit ihm verbundene Erfolgsmessung erfolgen auf Grundlage einer Einwilligung der Empfänger gem. Art. 6 Abs. 1 lit. a, Art. 7 DSGVO i.V.m § 7 Abs. 2 Nr. 3 UWG oder falls eine Einwilligung nicht erforderlich ist, auf Grundlage unserer berechtigten Interessen am Direktmarketing gem. Art. 6 Abs. 1 lt. f. DSGVO i.V.m. § 7 Abs. 3 UWG. ")]),_c('p',[_vm._v(" Die Protokollierung des Anmeldeverfahrens erfolgt auf Grundlage unserer berechtigten Interessen gem. Art. 6 Abs. 1 lit. f DSGVO. Unser Interesse richtet sich auf den Einsatz eines nutzerfreundlichen sowie sicheren Newslettersystems, das sowohl unseren geschäftlichen Interessen dient, als auch den Erwartungen der Nutzer entspricht und uns ferner den Nachweis von Einwilligungen erlaubt. ")]),_c('p',[_vm._v(" Kündigung/Widerruf – Sie können den Empfang unseres Newsletters jederzeit kündigen, d.h. Ihre Einwilligungen widerrufen. Einen Link zur Kündigung des Newsletters finden Sie am Ende eines jeden Newsletters. Wir können die ausgetragenen E-Mailadressen bis zu drei Jahren auf Grundlage unserer berechtigten Interessen speichern bevor wir sie löschen, um eine ehemals gegebene Einwilligung nachweisen zu können. Die Verarbeitung dieser Daten wird auf den Zweck einer möglichen Abwehr von Ansprüchen beschränkt. Ein individueller Löschungsantrag ist jederzeit möglich, sofern zugleich das ehemalige Bestehen einer Einwilligung bestätigt wird. ")]),_c('h3',[_vm._v("Newsletter – Mailchimp")]),_c('p',[_vm._v(" Der Versand der Newsletter erfolgt mittels des Versanddienstleisters „MailGun“, einer Newsletterversandplattform des US-Anbieters Mailgun Technologies, Inc., 112 E Pecan St. #1135 San Antonio, TX 78205, USA. Die Datenschutzbestimmungen des Versanddienstleisters können Sie hier einsehen: "),_c('a',{attrs:{"href":"https://mailchimp.com/legal/privacy/","rel":"noreferrer noopener","target":"_blank"}},[_vm._v("https://www.mailgun.com/gdpr/")]),_vm._v(". Mailgun Technologies, Inc. ist unter dem Privacy-Shield-Abkommen zertifiziert und bietet hierdurch eine Garantie, das europäisches Datenschutzniveau einzuhalten ("),_c('a',{attrs:{"href":"https://www.privacyshield.gov/participant?id=a2zt0000000TO6hAAG&status=Active","rel":"noreferrer noopener","target":"_blank"}}),_c('a',{attrs:{"href":"https://www.privacyshield.gov/list"}},[_vm._v("https://www.privacyshield.gov/list")]),_vm._v("). Der Versanddienstleister wird auf Grundlage unserer berechtigten Interessen gem. Art. 6 Abs. 1 lit. f. DSGVO und 